<template>
  <div class="login-page">
    <div class="layer bg" id="login"></div>
    <div class="layer flex-center">
      <!-- logo -->
      <div class="logo-group">
        <img src="./image/logo.png" alt="logo">
      </div>
      <!--  -->
      <div class="form-group">
        <el-card>
          <el-form ref="loginForm" label-position="top" :rules="rules" :model="formLogin" size="default">
            <el-form-item  prop="username">
              <el-input type="text" v-model="formLogin.username" placeholder="">
                <i slot="prepend" class="fa fa-user-circle-o"></i>
              </el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input type="password" v-model="formLogin.password" placeholder="">
                <i slot="prepend" class="fa fa-keyboard-o"></i>
              </el-input>
            </el-form-item>
            <el-form-item prop="code">
              <el-input type="text" v-model="formLogin.code" placeholder="- - - -">
                <template slot="prepend"></template>
                <template slot="append">
                  <img class="login-code" src="./image/login-code.png">
                </template>
              </el-input>
            </el-form-item>
            <el-button size="default" @click="submit" type="primary" class="button-login"></el-button>
          </el-form>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
require('particles.js')
import config from './config/default'
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      // 
      dialogVisible: false,
      // 
      formLogin: {
        username: 'admin',
        password: '',
        code: 'v9am'
      },
      // 
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    particlesJS('login', config)
  },
  beforeDestroy () {
    if (pJSDom && pJSDom.length > 0) {
      pJSDom[0].pJS.fn.vendors.destroypJS()
      pJSDom = []
    }
  },
  methods: {
    ...mapActions('d2admin/account', [
      'login'
    ]),
    /**
     * @description 
     */
    // 
    submit () {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          // 
          this.login({
            vm: this,
            usernameOrEmailAddress: this.formLogin.username,
            password: this.formLogin.password
          })
        } else {
          this.$message.error('')
        }
      })
    }
  }
}
</script>

<style lang="scss">
@import './style.scss';
</style>
